<template>
  <container :title="title">
    <list :editRoute="userRoute" :show-del="true">
      <thead slot="thead">
      <tr>
        <th><checkbox v-model="checked" :checked="tgCheck"/></th>
        <th>{{lang.id}}</th>
        <th>{{lang.avatar}}</th>
        <th>{{lang.username}}</th>
        <th>{{lang.nickname}}</th>
        <th>{{lang.email}}</th>
        <th>{{lang.mobile}}</th>
        <th>{{lang.logins}}</th>
        <th>{{lang.status}}</th>
        <th width="205">{{$t('operation.name')}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
      <tr v-for="(vol,index) in list">
        <td><checkbox  v-model="checkbox" :checked="checked" :value="vol.id"/></td>
        <td>{{vol.id}}</td>
        <td>
          <img v-lazy="makeImage(vol.avatar)" style="width:50px;border-radius:50%;">
        </td>
        <td>{{vol.username}}</td>
        <td>{{vol.nickname}}</td>
        <td>{{vol.email}}</td>
        <td>{{vol.mobile}}</td>
        <td>{{vol.logins}}</td>
        <v-td type="status" :value="vol.status" />
        <v-td type="opt" :value="vol.id"
              authName="授权角色"
              authRoute="auth_edit"
              edit-route="user_create_edit" />
      </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List, VTd, Checkbox} from "@/components"


  export default {
    name: "index",
    components: {VTd, List, Container,Checkbox},
    data(){
      return{
        title:this.$t('user.index'),
        lang:this.$t('user'),
        userRoute:{
          name:"user_create_edit"
        },
        checked:false,
        tgCheck:false,

      }
    },
    watch:{
      checkbox(newValue){
        const length = newValue.length
        if (length === this.list.length){
          this.tgCheck = true
        }
        if (length === 0) {
          this.tgCheck = false
        }
      }
    },
    computed:{
      list(){
        return this.$store.state.table.list
      },
      checkbox:{
        get(){
          return this.$store.state.table.checkbox
        },
        set(value){
          this.$store.commit('table/SET_CHECKBOX',value)
        }
      }
    },
    methods:{
      makeImage(image_name){
        return '/api/download/'+image_name
      }
    },
    mounted(){
      const url = '/api/user/index'
      this.$store.commit('table/SET_URL', url)
      this.$store.dispatch('table/fetchList')
    }
  }
</script>



